$(".filter__item").click(function () {
    $(".filter__item").removeClass("_active")
    $(this).addClass("_active")
    if($(this).hasClass("_active")){
        sortProducts($(this))
    }
})


function sortProducts(filterItem) {
    if($(filterItem).hasClass("_reverse")){
        $(filterItem).removeClass("_reverse")
    } else{
        $(filterItem).addClass("_reverse")
    }

    if($(filterItem).hasClass("_popular-filter")){
        popularSort()
    }
    if($(filterItem).hasClass("_price-filter")){
        priceSort()
    }
}



function popularSort() {
    let products = document.querySelectorAll(".product-card")
    products.forEach(product => {
        let order = +product.getAttribute("data-popularity-order")
        if($("._popular-filter").hasClass("_reverse")){
            product.style.order = order
        } else{
            product.style.order = -1 * order
        }
    })
}

function priceSort() {
    let products = document.querySelectorAll(".product-card")
    products.forEach(product => {
        let order = +product.getAttribute("data-price-order")
        if($("._price-filter").hasClass("_reverse")){
            product.style.order = order
        } else{
            product.style.order = -1 * order
        }
    })
}
