const parallaxItems = document.querySelectorAll(".parallax-item:not(._static)")

if(parallaxItems.length > 0){
    parallaxItems.forEach(item => {
        const forMainImg = 20;
        const speed = 2

        let positionX = 0
        let cordXPercent = 0
        let positionY = 0
        let cordYPercent = 0

        function setMouseParallaxStyle() {
            const distX = cordXPercent - positionX
            const distY = cordYPercent - positionY

            positionX = positionX + (distX * speed)
            positionY = positionY + (distY * speed)

            item.style.cssText = `transform: translate(${positionX/forMainImg}%,${positionY/forMainImg}%);`
            requestAnimationFrame(setMouseParallaxStyle)
        }

        setMouseParallaxStyle()

        document.addEventListener("mousemove", function (e) {
            const parallaxWidth = window.innerWidth
            const parallaxHeight = window.innerHeight

            const cordX = e.pageX - parallaxWidth/2;
            const cordY = e.pageX - parallaxHeight/2;

            cordXPercent = cordX / parallaxWidth * 100
            cordYPercent = cordY / parallaxHeight * 100

            item.style.cssText = `transform: translate(${positionX/forMainImg}%,${positionY/forMainImg}%);`
        })
    })
}
